<script>
import axios from "axios";
import Portfolio from "@/components/Portfolio";
import Message from "@/components/Message";

import moment from "moment";
moment.locale("cs");

export default {
  name: "page",
  components: {
    Message,
    Portfolio
  },
  data() {
    return {
      portfolioLinks: [],
      loading: true,
    };
  },
  created() {
    axios
      .get(
        "items/content?fields=*,date,heading,tags,intro,intro_img.title,intro_img.data.thumbnails&filter[tags][like]=grafika,fotografie"
      )
      .then((res) => {
        if (res.status == 200) {
          this.portfolioLinks = res.data.data;
          this.loading = false;
        } else {
          //some error handling
        }
      });
  },
  filters: {
    moment: function (date) {
      return moment(date).format("MMMM YYYY");
    },
  },
};
