<template>
  <div class="portfolio__link">
    <a :href="'/'+ item.type + '/' + item.slug" class="portfolio__link-href">
      <img :src="item.intro_img.data.thumbnails[7].url" :alt="item.intro_img.title" class="portfolio__link-image" />
    </a>
    <div class="portfolio__link-text">
      <a :href="'/'+ item.type + '/' + item.slug">
        <h3 class>{{item.heading}}</h3>
      </a>
      <p>
        <span v-for="(tag, index) in item.tags" :key="index">
          #{{tag}}
          <span v-if="index+1 < item.tags.length"> </span>
        </span>
      </p>
      <p>{{item.intro}}</p>
      <p>{{item.date | moment}}</p>
    </div>
  </div>
</template>

<script>
import moment from 'moment';
moment.locale('cs');
export default {
  props: ["item"],
  data() {
    return {};
  },
  filters: {
  moment: function (date) {
    return moment(date).format('MMMM YYYY');
  }
}
};
</script>
<style scoped>
.portfolio__link {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0 48px;
  margin-bottom: 48px;
  align-items: center;
}

.portfolio__link-href {
  grid-column: span 3;
}

.portfolio__link-image {
  height: 280px;
  width: 100%;
  object-fit: cover;
  border-radius: 48px;
}

.portfolio__link-text {
  padding: 0 24px;
  text-align: left;
  grid-column: span 3;
}

@media only screen and (min-width: 800px) {
  .portfolio__link-href {
    grid-column: span 2;
  }

  .portfolio__link-text {
    padding: 0;
    grid-column: span 1;
    text-align: left;
  }

  .portfolio__link:nth-child(even) > .portfolio__link-text {
    order: 1;
    text-align: right;
  }

  .portfolio__link:nth-child(even) > .portfolio__link-href {
    order: 2;
  }
}
</style>