<template>
  <div>
    <agile
      class="content__image"
      ref="content__image"
      :dots="false"
      :navButtons="false"
      :fade="true"
    >
      <div
        class="slide"
        v-for="(slide, index) in slides"
        :key="index"
        :class="`slide--${index}`"
      >
        <img :src="slide.directus_files_id.data.thumbnails[7].url" />
      </div>
    </agile>
    <agile
      class="content__thumbnails"
      ref="content__thumbnails"
      :options="thumbnailsOptions"
    >
      <div
        class="slide"
        v-for="(slide, index) in slides"
        :key="index"
        :class="`slide--${index}`"
        @click="
          $refs.content__image.goTo(index);
          $refs.content__thumbnails.goTo(index);
        "
      >
        <img :src="slide.directus_files_id.data.thumbnails[2].url" :alt="slide.directus_files_id.data.title"/>
      </div>
    </agile>
  </div>
</template>

<script>
import { VueAgile } from "vue-agile";
export default {
  name: "Content",
  components: {
    agile: VueAgile,
  },
  data() {
    return {
      asNavFor1: [],
      asNavFor2: [],
      thumbnailsOptions: {
        asNavFor: [this.$refs.content__image],
        dots: false,
        navButtons: false,        
				centerMode: true,
        speed: 2000,
        autoplay: true,
        autoplaySpeed: 3000,
        slidesToShow: 2,
        responsive: [
          {
            breakpoint: 400,
            settings: {
              slidesToShow: 3,
            },
          },
          {
            breakpoint: 800,
            settings: {
              slidesToShow: 3,
            },
          },
          {
            breakpoint: 1200,
            settings: {
              slidesToShow: 5,
            },
          },
          {
            breakpoint: 1600,
            settings: {
              slidesToShow: 7,
            },
          }
        ],
      },
    };
  },
  props: ["slides"],
};
</script>
<style>
.agile img {
  object-fit: cover;
}

.agile__nav-button {
  background: transparent;
  border: none;
  color: #ccc;
  cursor: pointer;
  font-size: 24px;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.agile__nav-button:hover {
  color: #888;
}
.agile__dot {
  margin: 0 10px;
}
.agile__dot button {
  background-color: #eee;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: block;
  height: 10px;
  font-size: 0;
  line-height: 0;
  margin: 0;
  padding: 0;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  width: 10px;
}
.agile__dot--current button,
.agile__dot:hover button {
  background-color: #888;
}

.slide {
  -webkit-box-align: center;
  align-items: center;
  box-sizing: border-box;
  color: #fff;
  display: -webkit-box;
  display: flex;
  height: 45vh;
  -webkit-box-pack: center;
  justify-content: center;
}

.content__image {
  padding-bottom: 24px;
}

.content__image .slide {
  padding: 0 12px;
}

.content__thumbnails .slide {
  cursor: pointer;
  height: 20vh;
  padding: 0 12px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border-radius: 48px;
}


.content__thumbnails .slide:hover {
  opacity: 0.75;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}

.slide img {
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center;
  object-position: center;
  width: 100%;
  border-radius: 48px;
}

.content__image .slide img {
  height: 100%;
  -o-object-fit: contain;
  object-fit: contain;
  -o-object-position: center;
  object-position: center;
  width: 100%;
  border-radius: 0px;
}

.content__thumbnails .slide img {
  border-radius: 24px;
}
</style>
