<template>
  <div class="page">
    <div v-if="loading" class="page__loader">
      <div class="loader">Loading...</div>
    </div>
    <h1>{{ $route.params.type }}</h1>
    <div class="page__info">
      <span>
        <a href="/" class="page__info-type">Domů</a> >
        <em>
          <span class="page__info-type">{{ $route.params.type }}</span>
        </em>
      </span>
    </div>
    <transition-group name="insert-portfolio" v-if="portfolioLinks" tag="div">
      <PortfolioLink v-for="(item, index) in portfolioLinks" :key="index" :item="item"></PortfolioLink>
    </transition-group>
  </div>
</template>

<script>
import axios from "axios";
import PortfolioLink from "@/components/PortfolioLink";

import moment from "moment";
moment.locale("cs");

export default {
  name: "page",
  components: {
    PortfolioLink,
  },
  data() {
    return {
      portfolioLinks: [],
      loading: true,
    };
  },
  created() {
    axios
      .get(
        "items/content?fields=*,date,heading,tags,intro,intro_img.title,intro_img.data.thumbnails&filter[type][eq]=" +
          this.$route.params.type
      )
      .then((res) => {
        if (res.status == 200) {
          this.portfolioLinks = res.data.data;
          this.loading = false;
        } else {
          //some error handling
        }
      });
  },
  filters: {
    moment: function (date) {
      return moment(date).format("MMMM YYYY");
    },
  },
};
</script>
