.page {
  padding: 0 48px;
}

.page > div {
  padding: 24px 0;
}

.page h1 {
  font-size: 4rem;
  text-align: center;
  letter-spacing: 0;
  margin: 64px 0 0 0;
  text-transform: capitalize;
}

.page__info {
  text-align: center;
}

.page__info > span {
  padding: 0 12px;
}

.page__info-type {
  text-transform: capitalize;
}

