<template>
  <div>
    <div v-if="loading" class="content__loader">
      <div class="loader">Loading...</div>
    </div>
    <transition name="fade">
      <div class="content" v-if="!loading">
        <h1 class="content__heading">{{ item.heading }}</h1>
        <div class="content__info">
          <span><a href="/" class="content__info-type">Domů</a> > <a :href="'/' + item.type" class="content__info-type">{{ item.type }}</a> > <em>{{item.heading | shorten}}</em></span>
          <span>{{ item.date | moment }}</span>
          <span>
            <span v-for="(tag, index) in item.tags" :key="index">
              #{{ tag }}
              <span v-if="index + 1 < item.tags.length"></span>
            </span>
          </span>
          <span>{{ item.customer }}</span>
        </div>
        <div class="content__slider" v-if="item.slideshow.length">
          <Slider :slides="item.slideshow"></Slider>
        </div>
        <div class="content__intro">{{item.intro}}</div>
        <div class="content__html" v-html="item.content"></div>
      </div>
    </transition>
  </div>
</template>

<script>
import axios from "axios";
import Slider from "@/components/Slider";

import moment from "moment";
moment.locale("cs");

export default {
  name: "Content",
  components: {
    Slider,
  },
  data() {
    return {
      item: {},
      loading: true,
    };
  },
  created() {
    axios
      .get(
        "items/content?fields=content,date,customer,heading,tags,type,intro,intro_img.title,intro_img.data.thumbnails,slideshow.directus_files_id.title,slideshow.directus_files_id.data.thumbnails&filter[slug][eq]=" +
          this.$route.params.slug +
          "&single"
      )
      .then((res) => {
        if (res.status == 200) {
          this.item = res.data.data[0];
          this.loading = false;
        }
      })
      .catch(() => {
        window.location.replace('/' + this.$route.params.type);
      });
  },
  filters: {
    moment: function (date) {
      return moment(date).format("MMMM YYYY");
    },
    shorten: function (text) {
      var textArray = text.split(" ");
      if (textArray.length > 3) {
        text = textArray.slice(0,3).join(" ") + "..."
      }
      return text;
    }
  },
};
</script>
